import 'vux/src/styles/reset.less';
import './index.scss'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import utils from 'js/utils.js'

import { hostImg } from 'js/host-config'
import { fetch, rap } from 'js/fetch.js'


let url = {
getAD: 'api/ads',
getCategories: 'api/book/categories',
getBooks: 'api/books',
getHomeRecommend:'api/books?type=home_recommend'
}

url = rap(url)
import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
ADList: [],
categoriesList: [],
listIndex: 0,
transitionName: 'slide-left',
isLogin: '',
bookList: [],
mylist: [], //临时存放
demo4Value: {
pullupStatus: 'default'
},
lists: [{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
}]
},
created() {
this.isLogin = utils.isLogin()
this.getADList()
this.getHomeRecommend()
},
mounted() {
},
methods: {
load4() {
alert(1)
this.demo4Value.pullupStatus = 'up'
setTimeout(() => {
this.bookList.push({
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
})
setTimeout(() => {
this.demo4Value.pullupStatus = 'default'
this.$nextTick(() => {
this.$refs.scroller.reset()
})
}, 10)
}, 2000)
},
show(index) {


if (this.listIndex > index) {
this.transitionName = 'slide-right'
} else if (this.listIndex > index) {
this.transitionName = 'slide-left'
}
this.listIndex = index
},
getADList() {

fetch('get', url.getAD).then(res => {
if (res.status === 200) {
res.data.forEach((item, index) => {
let obj = {
img: `${hostImg}${item.ad_image}`,

/*title: `${item.name}`*/
}
this.ADList.push(obj)
})
} else {

}
})
},
getHomeRecommend() {

fetch('get', `${url.getHomeRecommend}`, {
}).then(res => {
if (res.status >= 200 && res.status <= 500) {
console.log(res)
res.data.forEach((item,index)=>{
item.cover = `${hostImg}${item.cover}`
})
this.bookList=res.data
}
})
},
getCategories() {

let _this = this
fetch('get', url.getCategories, {
params: {
pid: 0,
}
}).then(res => {
if (res.status >= 200 && res.status <= 500) {
this.categoriesList = res.data

res.data.forEach(function (item, index) {





_this.getbook({ category_id: item.id })
})
}
})
},
_getbook(config) {
return fetch('get', url.getBooks, { params: config })
},
getbook(config = null) {

let _this = this
fetch('get', url.getBooks, {
params: config
}).then(res => {

if (res.status >= 200 && res.status <= 500) {

res.data.forEach((item, index) => {
item.cover = `${hostImg}${item.cover}`
})
_this.mylist[config.category_id - 1] = res.data
_this.$set(_this, 'bookList', _this.mylist);
}

})
},
goDetail(id) {
window.location.href = `./books_detail.html?id=${id}`
},
goRegister() {

if (this.isLogin) {
window.location.href = './buy_member.html'
} else {
window.location.href = './register.html'
}
}
},
components: {
Swiper,
Scroller,
Spinner,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
ViewBox
}
,
mixins: [mixin]
})



